/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-gutenpride input[type="text"] {
	font-family: Gilbert, sans-serif;
	font-size: 64px;
	color: inherit;
	background: inherit;
	border: 0;
}

.message-block-wrap{
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 20px;
	width: 100%;
	.column{
		width: 100%;
	}
}
.accordion_editor{
	border: 1px solid grey;
	h3{
		margin-top: 0;
	}
	.accordion-block-title{
		input{
			font-size: 25px;
		}
	}
	.components-text-control__input{
		border: 0;
		border-bottom: 1px solid grey;
		border-radius: 0;
		background-color: transparent;
	}
	textarea.block-editor-plain-text{
		min-height: 150px;
		border:1px solid grey;
	}
}
.accordion {
	position: relative;
	margin-bottom: 20px;
	
	.accordion-show-btn{
		position: absolute;
		top: 0;
		right: 0;
		width: 30px;
		height: 30px;
		border: 0;
		background-color: #1e1e1e;
		color:#fff;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		border-radius: 5px;
		&:hover{
			background-color: #007cba;
		}
	}
	:last-child{
		margin-bottom: 0;
		.accordion-title{
			margin-bottom: 0;
		}
	}
}
